.center {
    display: flex;
    align-items: center;
    justify-content: center;
    /* text-align: center; */
}

#avtar-img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 15%;
    margin-bottom: auto;
    height: 50vh;
    /* background-image: radial-gradient(circle, red, yellow, green); */
    background-color: rgb(0, 255, 255);
    border: 0.4em solid rgb(0, 255, 0);
    padding: 15px;
    border-radius: 50% 50% 50% 50% / 50% 50% 50% 50%;
}

@media screen and (max-device-width: 480px) and (orientation: portrait) {
    #avtar-img {
        display: block;
        margin-left: auto;
        margin-right: auto;
        margin-top: 25%;
        margin-bottom: auto;
        height: 45vh;
        /* background-image: radial-gradient(circle, red, yellow, green); */
        background-color: rgb(0, 255, 255);
        border: 0.4em solid rgb(0, 255, 0);
        padding: 15px;
        border-radius: 50% 50% 50% 50% / 50% 50% 50% 50%;
    }
}